.content{display:flex;flex-direction:column;align-items:center;justify-content:center;gap:50px;padding-top:60px}@keyframes fadeIn{0%{opacity:0;transform:translateY(-20px)}100%{opacity:1;transform:translateY(0)}}.Title{animation:fadeIn .7s ease-out}.link{color:#000;text-decoration:none}.blocks{display:flex;align-items:center;justify-content:center;flex-direction:row;gap:20px}.StudentsPage{padding:2rem 0;background-color:#f5f5f5}.container{max-width:1200px;margin:0 auto;position:relative}.heroContent{display:flex;justify-content:space-between;align-items:flex-start;position:relative}.StudentsMainContent,.blocks{opacity:0;transform:translateX(-5%);transition:opacity .5s ease-out,transform .5s ease-out}.StudentsMainContent{transition-delay:.5s}.blocks{transform:translateX(5%);transition-delay:.5s}.StudentsMainContent.visible,.blocks.visible{opacity:1;transform:translateX(0)}.heroContent{display:flex;gap:50px;align-items:center;justify-content:center;flex-direction:row;transition:flex-direction .3s}@media(max-width: 1190px){.heroContent{gap:20px}}@media(max-width: 1140px){.heroContent{gap:18px}}@media(max-width: 1100px){.heroContent{flex-direction:column}.blocks{order:0;gap:15px}.StudentsMainContent{order:1}.Title{font-size:25px}}@media(max-width: 890px){.content{gap:30px}.blocks{order:0}.StudentsMainContent{order:1}}@media(max-width: 380px){.Title{font-size:20px}}